import Vue from 'vue'
import Router from 'vue-router'
import AppIndex from '../components/home/AppIndex'
import Login from '../components/Login'
import Home from '../components/Home'
import LibraryIndex from '../components/library/LibraryIndex'
import Test from '../components/test/Test'
import Layout1 from '../components/test/Layout1'
import AutoInput from '../components/test/AutoInput'
import SelectVue from '../components/test/SelectVue'
import CascaderVue from '../components/test/CascaderVue'
import TimePicker from '../components/test/TimePicker'
import Form from '../components/test/Form'
import Header from '../components/test/Header'
import Register from '../components/test/Register'
import Repass from '../components/test/Repass'
import Repass2 from '../components/test/Repass2'
import Repass3 from '../components/test/Repass3'
import ManageHome from '../components/ManageHome'
import TaskIndex from '../components/task/TaskIndex'
Vue.use(Router)
const Foo = {template: '<div>foo</div>'}
const Bar = {template: '<div>bar</div>'}
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'index',
      redirect: '/index',
      component: AppIndex,
      meta: {
        requireAuth: true
      }
    },
    {
      path: '/home',
      name: 'Home',
      component: Home,
      // home页面并不需要被访问
      // redirect: '/index',
      children: [
        {
          path: '/index1',
          name: 'AppIndex',
          component: AppIndex,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/library',
          name: 'Library',
          component: LibraryIndex,
          meta: {
            requireAuth: true
          }
        }
      ]
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/foo',
      component: Foo
    },
    {
      path: '/bar',
      component: Bar
    },
    {
      path: '/layout',
      name: 'Layout1',
      component: Layout1,
      children: [
        {
          path: '/test',
          name: 'Test',
          component: Test
        },
        {
          path: '/form1',
          name: 'Form',
          component: Form
        },
        {
          path: '/select',
          name: 'SelectVue',
          component: SelectVue
        }
      ]
    },
    {
      path: '/auto',
      name: 'AutoInput',
      component: AutoInput
    },
    {
      path: '/select',
      name: 'SelectVue',
      component: SelectVue
    },
    {
      path: '/cascader',
      name: 'CascaderVue',
      component: CascaderVue
    },
    {
      path: '/time',
      name: 'TimePicker',
      component: TimePicker
    },
    {
      path: '/header',
      name: 'Header',
      component: Header,
      children: [
        {
          path: '/login',
          name: 'Login',
          component: Login
        },
        {
          path: '/register',
          name: 'Register',
          component: Register
        },
        {
          path: '/repass',
          name: 'Repass',
          component: Repass
        },
        {
          path: '/repass2',
          name: 'Repass2',
          component: Repass2
        },
        {
          path: '/repass3',
          name: 'Repass3',
          component: Repass3
        }
      ]
    },
    {
      path: '/manage',
      name: 'ManageHome',
      component: ManageHome,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'AppIndex',
          component: AppIndex,
          meta: {
            requireAuth: true
          }
        },
        {
          path: '/task',
          name: 'TaskIndex',
          component: TaskIndex
        }
      ]
    }
  ]
})



