<template>
  <div >
    <el-form class="login-container" label-position="left"
             label-width="50px">
      <h3 class="login_title">系统登录</h3>
      <el-form-item label="账号">
        <el-input type="text" v-model="loginForm.username"
                  auto-complete="off" placeholder="账号" ></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="loginForm.password"
                  auto-complete="off" placeholder="密码" ></el-input>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-button type="primary" style="width: 100%;background: #505458;border: none" v-on:click="login">登录</el-button>
      </el-form-item>
      <el-form-item style="width: 100%">
        <el-row :gutter="10">
          <el-col :span="8">
            <el-link type="info" href="/register" style="text-align: left">注册</el-link>
          </el-col>
          <el-col :span="8">
            <el-link type="info"  href="/repass" style="text-align: right">忘记密码?</el-link>
          </el-col>
        </el-row>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'Login',
    data () {
      return {
        loginForm: {
          username: 'lamaxiya',
          password: '6666666'
        },
        responseResult: []
      }
    },
    methods: {
      login () {
        var _this = this
        this.$axios
          .post('/login', {
            username: this.loginForm.username,
            password: this.loginForm.password
          })
          .then(successResponse => {
            if (successResponse.data.code === 200) {
              // var data = this.loginForm
              this.$router.replace('/form1')
            }
            if (successResponse.data.code === 400) {
              _this.$message.error('账号错误')
            }
          })
          .catch(failResponse => {
            _this.$message.error('登陆出错')
          })
      }
    }
  }
</script>
<style>
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0 auto 40px auto;
    text-align: center;
    color: #505458;
  }
  #poster {
    background:url("../assets/header.jpg") ;
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
</style>
<style scoped>

</style>



